import React from 'react';

const HomeAboutArea = () => {
    return (
        <>
            <section className="about-area pt-120 pb-90">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-6 col-lg-5 about_left">
                            <div className="about-left-side pos-rel mb-30">
                                <div className="about-front-img  img-fuild">
                                    <img src="/img/about/manufacturing.jpg" alt="" />
                                </div>
                                <div className="about-shape">
                                    <img src="img/about/about-shape.png" alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-6 col-lg-7">
                            <div className="about-right-side pt-55 mb-30">
                                <div className="about-title mb-20">
                                    <h1 className='font-size-50'>Manufacturing facilities</h1>
                                </div>
                                <div className="about-text">
                                    <p>Our all manufacturing facilities follows the international quality standards like WHO, GMP, GLP, CMP as well as ISO certification.The Good
                                        Manufacturing Practice and Good laboratory
                                        Practice certifiedall facilities provide us quality
                                        products with good therapeutic efficacy. Which
                                        gives us support and strength to follow our Aim. </p>
                                    <p>As we’ve an inclination to directly influence the health of the
                                        people, thus we’ve an inclination to pay major attention
                                        towards manufacturing the quality-oriented medicines. At
                                        our premises, a vigil check is conducted by the quality
                                        inspectors thus on make sure supreme quality of the endproducts.</p>
                                    <p>The safe medicines are formed in compliance with
                                        international standards and norms. Utmost care by
                                        maintaining the quality standards is taken during the
                                        manufacturing of the medicines.</p>
                                    <p>
                                        Quality analyst conducts quality check at different stage of
                                        productions by following the best healthcare methodologies.
                                        Maintenance of cleanliness and hygiene is maintained
                                        throughout the manufacturing methodology.
                                    </p>
                                    <p>
                                        Our Aim is to deliver the quality-based medicines, we’ve an
                                        inclination to produce and procure the material from the
                                        certified vendors.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default HomeAboutArea;