import React from 'react';
import { Carousel } from "react-responsive-carousel";

const ShopDetailsBanner = ({ product }) => {
   return (
      <>
         <section className="shop-banner-area pt-120 pb-90">
            <div className="container">
               <div className="row justify-content-between">
                  <div className="col-xl-6">
                     <Carousel showStatus={false}
                        renderArrowNext={(clickHandler, hasPrev) => {
                           return (<button
                              onClick={clickHandler}
                              className={
                                 "btn arrow-next"
                              }
                              type="button"
                           >
                              <svg width="50px" height="50px" viewBox="0 0 1024 1024" class="icon" version="1.1" xmlns="http://www.w3.org/2000/svg"><path d="M256 120.768L306.432 64 768 512l-461.568 448L256 903.232 659.072 512z" fill="#000000" /></svg>
                           </button>)

                        }}
                        renderArrowPrev={(clickHandler, hasPrev) => {
                           return (<button
                              onClick={clickHandler}
                              className={
                                 "btn arrow-prev"
                              }
                              type="button"
                           >
                              <svg xmlns="http://www.w3.org/2000/svg" width="50px" height="50px" viewBox="0 0 1024 1024" class="icon" version="1.1"><path d="M768 903.232l-50.432 56.768L256 512l461.568-448 50.432 56.768L364.928 512z" fill="#000000" /></svg>
                           </button>)

                        }}
                     // showThumbs={true}
                     >
                        {
                           product.image?.map((img) => (
                              <div>
                                 <div className="product-large-img">
                                    <img src={`/img/shop/${img.name}.jpg`} alt={img.name} />
                                 </div>
                              </div>
                           ))
                        }
                     </Carousel>
                  </div>
                  <div className="col-xl-5">
                     <div className="product-details mb-30">
                        <div className="product-details-title">
                           <h1>{product.name}</h1>
                        </div>
                        {product.description.map(d => (
                           <p>{d}</p>
                        ))}
                        <div className="product-cat mt-30 mb-30">
                           <span>Category: </span>
                           {product.catrgory}

                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </>
   );
};

export default ShopDetailsBanner;