import React from 'react';

const ShopDetailsDesc = ({ product }) => {
   return (
      <>
         <section className="product-desc-area pb-80">
            <div className="container">
               <div className="row">
                  <div className="col-12">
                     <div className="bakix-details-tab">
                        <ul className="nav text-center justify-content-center pb-30 mb-50" id="myTab" role="tablist">
                           <li className="nav-item">
                              <a className="nav-link active" id="id-add-in" data-bs-toggle="tab" href="#id-add" role="tab"
                                 aria-controls="profile" aria-selected="false">Additional Information</a>
                           </li>
                        </ul>
                     </div>
                     <div className="tab-content" id="myTabContent">
                        <div className="tab-pane fade show active" id="id-add" role="tabpanel" aria-labelledby="id-add-in">
                           <div className="additional-info">
                              <div className="table-responsive">
                                 <h4>Additional information</h4>
                                 <table className="table">
                                    <tbody>
                                       {product.additionalInformation.map(d => (
                                          <tr>
                                             <th>{d.name}</th>
                                             <td className="product_weight">{d.value}</td>
                                          </tr>
                                       ))}
                                    </tbody>
                                 </table>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
      </>
   );
};

export default ShopDetailsDesc;