import React from 'react';
import Slider from 'react-slick';

const HomeOurTeam = () => {
   const settings = {
      dots: true,
      infinite: false,
      speed: 300,
      arrows: false,
      slidesToShow: 5,
      slidesToScroll: 1,
      responsive: [
         {
            breakpoint: 1200,
            settings: {
               slidesToShow: 4,
               slidesToScroll: 1,
               infinite: true,
               dots: true
            }
         },
         {
            breakpoint: 600,
            settings: {
               slidesToShow: 2,
               slidesToScroll: 1
            }
         },
         {
            breakpoint: 480,
            settings: {
               slidesToShow: 2,
               slidesToScroll: 1
            }

         }
      ]
   };

   return (
      <>
         <section className="team-area pt-115">
            <div className="container">
               <div className="row">
                  <div className="col-xl-12 col-lg-12 col-md-12">
                     <div className="section-title text-center mb-75">
                        <div className="section-text pos-rel">
                           <h3>Our Brand</h3>
                        </div>
                        <div className="section-line pos-rel">
                           <img src="img/shape/section-title-line.png" alt="" />
                        </div>
                     </div>
                  </div>
               </div>
               <div className="row">
                  <Slider className='testimonials-activation team__area' {...settings}>
                     <div className='w-60'>
                        <img src="img/brand/brand-1.jpg" alt='brand-1' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-2.jpg" alt='brand-2' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-3.jpg" alt='brand-3' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-4.jpg" alt='brand-4' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-5.jpg" alt='brand-1' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-6.jpg" alt='brand-2' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-7.jpg" alt='brand-3' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-8.jpg" alt='brand-4' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-9.jpg" alt='brand-4' />
                     </div>
                     <div className='w-60'>
                        <img src="img/brand/brand-10.jpg" alt='brand-4' />
                     </div>
                  </Slider>
               </div>
            </div>
         </section>
      </>
   );
};

export default HomeOurTeam;