import React from 'react';
import { useParams } from 'react-router-dom';
import CommonPageHeader from '../../../components/CommonPageHeader/CommonPageHeader';
import { Product } from '../../../config';
import HomeFooter from '../../Home/Home/HomeFooter/HomeFooter';
import HomeHeader from '../../Home/Home/HomeHeader/HomeHeader';
import ShopDetailsBanner from './ShopDetailsBanner/ShopDetailsBanner';
import ShopDetailsDesc from './ShopDetailsDesc/ShopDetailsDesc';

const ShopDetails = () => {
    const Parms = useParams()
    console.log(Parms, Parms?.id.replace(new RegExp("-", "gm"), " ").toLowerCase())
    const SingleProduct = Product.find((e) => {
        console.log(e.name.toLowerCase(), Parms?.id.replace(new RegExp("-", "gm"), " ").toLowerCase())
        return e.name.toLowerCase() === Parms?.id.replace(new RegExp("-", "gm"), " ").toLowerCase()
    })
    console.log(SingleProduct)

    return (
        <>
            <HomeHeader />
            <CommonPageHeader title="Product Details" subtitle="Details" />
            <ShopDetailsBanner product={SingleProduct} />
            <ShopDetailsDesc product={SingleProduct} />
            <HomeFooter />
        </>
    );
};

export default ShopDetails;